@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
.title {
  display: flex;
  justify-content: center;
  padding: 2%;
  font-size: 3em;
  color: white;
  background: #00046b; }

body > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: -webkit-fill-available; }

.bg {
  flex-grow: 1;
  background: #f7f7f7;
  border-bottom: 2px solid #767676; }

.members {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 1em;
  margin: 0em 20%;
  padding: 1em 0em; }
  @media screen and (min-width: 650px) {
    .members {
      grid-template-columns: repeat(3, 1fr); } }

.member {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px; }

.avatar {
  width: 150px;
  border-radius: 80px;
  margin: 1em 0em; }

.avatar:hover {
  box-shadow: 0 5px 10px -2px #1e2225; }

body {
  font-family: 'Roboto';
  margin: 0; }
